import './Project.css';
import React, { useRef } from 'react';
import { Container } from "react-bootstrap";
import moon from '../assets/img/moon.png';
import land from '../assets/img/land.png';
import cat from '../assets/img/cat.gif';
import { Parallax, ParallaxLayer } from '@react-spring/parallax';

export const Project = () => {
  const ref = useRef();

  return (
    <div className="project-container">
      <Parallax pages={4} ref={ref}>
        {/* Background Layers */}
        <ParallaxLayer
          offset={0}
          speed={1}
          factor={2}
          style={{
            backgroundImage: `url(${moon})`,
            backgroundSize: 'cover',
          }}
        />
        <ParallaxLayer
          offset={2}
          speed={1}
          factor={4}
          style={{
            backgroundImage: `url(${land})`,
            backgroundSize: 'cover',
          }}
        />

        {/* Sticky Cat Image */}
        <ParallaxLayer
          sticky={{ start: 0.9, end: 2.5 }}
          style={{ textAlign: 'center' }}
        >
          <img src={cat} alt="Cat" className="cat-image" />
        </ParallaxLayer>

        {/* Clickable Text Layers */}
        <ParallaxLayer
          offset={0.2}
          speed={0.05}
          onClick={() => ref.current.scrollTo(3)}
        >
          <h2 className="clickable-text">Welcome to my website</h2>
        </ParallaxLayer>
        <ParallaxLayer
          offset={3}
          speed={2}
          onClick={() => ref.current.scrollTo(0)}
        >
          <h2 className="clickable-text">Hi Mom!</h2>
        </ParallaxLayer>
      </Parallax>
    </div>
  );
}
