import { Container, Row, Col } from "react-bootstrap";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import meter3 from "../assets/img/meter3.svg";
import colorSharp from "../assets/img/color-sharp.png";

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 5
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };

  return (   
    
    <section className="skill" id="skills">
      <br/>
      <br/>
      <Container>
        <Row>
          <Col>
            <div className="skill-bx-background">
              <h2><span className="tagline">Technical Skills</span></h2>
            </div>
            <div className="skill-bx-background">
              <hr/>
              <h3>Languages</h3>
              <hr/>
              <Carousel 
                responsive={responsive} 
                infinite={true} 
                className="skill-slider"
                autoPlay={true} 
                autoPlaySpeed={6000}
                arrows={true}
              >
                <div className="item">
                  <h5>C++</h5>
                </div>
                <div className="item">
                  <h5>Python</h5>
                </div>
                <div className="item">
                  <h5>Rust</h5>
                </div>
                <div className="item">
                  <h5>JavaScript</h5>
                </div>
                <div className="item">
                  <h5>HTML/CSS</h5>
                </div>
              </Carousel>
              <br/>
              <hr/>
              
              <h3>Frameworks and Libraries</h3>
              <hr/>
              <Carousel 
                responsive={responsive} 
                infinite={true} 
                className="skill-slider"
                autoPlay={true} 
                autoPlaySpeed={6500}
                arrows={true}
              >
                <div className="item">
                  <h5>React.js</h5>
                </div>
                <div className="item">
                  <h5>Node.js</h5>
                </div>
                <div className="item">
                  <h5>Express.js</h5>
                </div>
                <div className="item">
                  <h5>Django</h5>
                </div>
              </Carousel>
              <br/>
              <hr/>
              <h3>Database Management</h3>
              <hr/>
              <Carousel 
                responsive={responsive} 
                infinite={true} 
                className="skill-slider"
                autoPlay={true} 
                autoPlaySpeed={7000}
                arrows={true}
              >
                <div className="item">
                  <h5>MySQL</h5>
                </div>
                <div className="item">
                  <h5>PostgreSQL</h5>
                </div>
                <div className="item">
                  <h5>MongoDB</h5>
                </div>
              </Carousel>
            </div>
            
          </Col>
        </Row>
      </Container>
      {/* <img className="background-image-left" src={colorSharp} alt="color gradient" /> */}
    </section>
  )
}
